.lfbutton {
  width: 113px;
  height: 41px;

  background-color: var(--brandcolor);
  color: white;

  font-size: 14px;
  font-weight: 300;

  outline: none;
}

.lfbutton:focus {
  text-decoration: underline;
}
.lfbutton:hover {
  text-decoration: underline;
}

.lfbutton:active {
  background-color: var(--brandcolor-light);
}

.lfbutton:disabled {
  color: var(--disabled-fg);
  background-color: var(--disabled-bg);
}



