.lflegal-wrap {
  font-size: 12px;
  color: var(--input-label);
  text-align: left;
}

.lflegal-wrap a:link {
  font-weight: bold;
  color: var(--input-label);
  text-decoration: none;
}

.lflegal-wrap a:visited {
  color: var(--input-label);
  font-weight: bold;
}

.lflegal-wrap a:hover {
  color: var(--brandcolor);
}

.lflegal-wrap a:active {
  color: var(--brandcolor);
}



