:root {
  --brandcolor: #3f2667;
  --brandcolor-light: #6c41b3;

  --input-border: rgba(63, 38, 103, 0.18);
  --input-label: rgba(63, 38, 103, 0.44);
  --input-error: #c648c6;

  --loader-light: #ffffff;
  --loader-dark: #faf7ff;

  --disabled-bg: #faf7ff;
  --disabled-fg: #b4aebd;
}

.grey500 {
  color: #9e9e9e;
}

.brandcolor {
  color: var(--brandcolor);
}



